/* Swiper 컨테이너 */
.swiper {
    height: 100px;
    /* 슬라이드 높이 조절 */
    margin: 5px;
    /* 아래 제목과의 간격 */
}

/* Swiper 슬라이드 */
.swiper-slide {
    background-color: #3d79cd;
    /* 배경색 변경 */
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 15px;
    /* 둥근 모서리 강조 */
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    /* 그림자 효과 */
    font-weight: bold;
    color: #ffffff;
    /* 글자 색상 변경 */
    transition: transform 0.5s ease, box-shadow 0.3s ease;
    overflow: hidden;
    /* 이미지 넘침 방지 */
}

/* 이미지 추가 및 스타일 */
.swiper-slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* 이미지 비율 유지하며 채우기 */
}

/* 활성 슬라이드 */
.swiper-slide-active {
    transform: scale(1.2);
    /* 더 크게 확대 */
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.3);
}

/* 다음/이전 슬라이드 */
.swiper-slide-next,
.swiper-slide-prev {
    filter: brightness(0.8);
    /* 밝기 조절 */
}

/* Swiper 제목 스타일 */
.swiper-title {
    font-size: 18px;
    /* 글자 크기 */
    font-weight: bold;
    /* 굵게 */
    text-align: center;
    /* 가운데 정렬 */
    margin-bottom: 20px;
    /* 슬라이더와의 간격 */
    color: #333;
    /* 글자 색상 */
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.2);
    /* 그림자 효과 */
}


/* Swiper 설명 스타일 */
.swiper-desc {
    font-size: 16px;
    text-align: center;
    margin-top: 20px;
    overflow: hidden; /* 넘치는 텍스트 숨김 */
  }
  
  /* selectedValue 강조 스타일 */
  .swiper-desc span {
    font-weight: bold;
    color: rgb(42, 72, 210); 
  }

  .swiper-desc1 {
    text-align: center;
    overflow: hidden;
    color: rgb(42, 72, 210); 
    text-overflow: ellipsis;
    white-space: nowrap;
  }